/* Montserrat 300 */
@font-face {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 300;
    font-display: swap;
    src: local('Montserrat Light'), local('Montserrat-Light'),

      url('../fonts/montserrat-light/montserrat-light.woff') format('woff'),
      url('../fonts/montserrat-light/montserrat-light.ttf') format('truetype'),

  }
  
