.rest-main {
  padding-top: 16px;
  padding-bottom: 20px;
}
.rest-main__uppertitle {
  margin-bottom: 12px;
  font-family: 'Times New Roman';
  font-size: 20px;
  line-height: calc(23 / 20);
  color: var(--light-grey-color);
}
.rest-main__title {
  margin-bottom: 12px;
  font-family: 'Film P3';
  font-weight: 700;
  font-size: 40px;
  line-height: calc(43 / 40);
  letter-spacing: 0.3em;
  color: var(--dark-color);
}
.rest-main__undertitle {
  max-width: 258px;
  margin-bottom: 28px;
  font-weight: 600;
  font-size: 12px;
  line-height: calc(15 / 12);
}
.rest-wrap-1 {
  margin-bottom: 32px;
}
.interier-img {
  width: 244px;
  height: 322px;
  margin-right: 0;
  margin-left: auto;
  box-shadow: 2px 2px 60px 2px rgba(0, 0, 0, 0.25);
  border-radius: 2px;
}
@media screen and (min-width: 1024px) {
  .interier-img {
    width: 400px;
    height: 524px;
    box-shadow: 5px 5px 70px 17px rgba(0, 0, 0, 0.25);
  }
}
@media screen and (min-width: 1280px) {
  .interier-img {
    width: 460px;
    height: 574px;
  }
}
.restaurant-flex {
  margin-bottom: 32px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  @media screen and (min-width:320px) and (max-width:325px) {
  gap: 5px;
  }
}
.rest-main__text {
  transform: translateY(-18px);
  max-width: 163px;
  font-weight: 600;
  font-size: 12px;
  line-height: calc(15 / 12);
  letter-spacing: 0.02em;
  color: var(--last-accent-color);
  @media screen and (min-width:320px) and (max-width:325px) {
    width: 128px;
    }
}
.rest-slider-flex {
  transform: translateX(15px);
  display: flex;
  justify-content: flex-end;
  gap: 25px;
  margin-bottom: 20px;
}
.arrow-list {
  display: flex;
  justify-content: flex-end;
}
.interier-img-big {
  width: 200px;
  height: 278px;
  box-shadow: 2px 2px 60px 2px rgba(0, 0, 0, 0.25);
  border-radius: 2px;
}
@media screen and (min-width: 1024px) {
  .interier-img-big {
    width: 350px;
    height: 500px;
    box-shadow: 5px 5px 70px 17px rgba(0, 0, 0, 0.25);
  }
}
@media screen and (min-width: 1280px) {
  .interier-img-big {
    width: 460px;
    height: 574px;
    box-shadow: 2px 2px 60px 2px rgba(0, 0, 0, 0.25);
  }
}
.rest-img_1 {
  width: 245px;
  height: 245px;
  box-shadow: 2px 2px 60px 2px rgba(0, 0, 0, 0.25);
  border-radius: 2px;
}
@media screen and (min-width: 1024px) {
  .rest-img_1 {
    width: 194px;
    height: 194px;
  }
}
@media screen and (min-width: 1280px) {
  .rest-img_1 {
    width: 254px;
    height: 254px;
  }
}
.rest-img_2 {
  width: 60px;
  height: 245px;
  box-shadow: 2px 2px 60px 2px rgba(0, 0, 0, 0.25);
  border-radius: 2px;
}
@media screen and (min-width: 1024px) {
  .rest-img_2 {
    width: 194px;
    height: 194px;
  }
}
@media screen and (min-width: 1280px) {
  .rest-img_2 {
    width: 254px;
    height: 254px;
  }
}
@media screen and (min-width: 1024px) {
  .rest-wrap-1 {
    margin-bottom: 0;
    display: flex;
    justify-content: space-between;
    height: 360px;
  }
  .rest-main {
    padding-top: 120px;
    padding-bottom: 0;
  }
  .rest-main__uppertitle {
    font-size: 32px;
    line-height: calc(37 / 32);
  }
  .rest-main__title {
    font-size: 64px;
    line-height: calc(64 / 69);
  }
  .rest-main__undertitle {
    max-width: 439px;
    margin-bottom: 0;
    font-size: 16px;
    line-height: 20px;
  }
  .rest-main-pic-1 {
    transform: translate(-65px, -152px);
    margin-bottom: 0;
  }
  .restaurant-flex {
    gap: 42px;
    justify-content: flex-start;
    margin-bottom: 0;
  }
  .rest-main__text {
    font-size: 16px;
    line-height: calc(20 / 16);
    max-width: 312px;
  }
  .rest-slider-flex {
    margin-bottom: 32px;
  }
  .slider-wrap {
    transform: translate(-27px, -28px);
  }
}
@media screen and (min-width: 1280px) {
  .rest-main__undertitle {
    max-width: 518px;
  }
  .slider-wrap {
    transform: translate(-15px, -147px);
    height: 200px;
  }
}
